import chaobject from "@/components/script/chaobject";
import cha_list from "@/components/cha/item/cha_list";

export default {
  name: "history",
  components: {cha_list},
  mixins: [chaobject],
  props: {
    userinfo: undefined,
  },
  data() {
    return {
      cha_datas: undefined,
    }
  },
  created() {
    this.set_title('浏览历史');
    this.load_view_history({
      start: 0,
      limit: 30,
      cb: this.on_load_history,
      uid: this.userinfo.uid
    });
  },
  methods: {
    on_load_history: function (response) {
      console.log("on_load_history:", response);
      this.cha_datas = response.data.datas;
    }
  }
}
